@import "../settings/variables.scss";


.banner {
  position: relative;
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
    background-color: #fff;
    position: absolute;
    top: 80px;
    left: 0;
  }
}





